.parent {
    background-image: url('../img/login-bg.png');
    display: flex;
    justify-content: center;
    align-items: center;
    width: 785px;
    height: 543px;
}

.container {
    position: relative;
    width: 50%;
    height: 50%;
    background-color: white;
    opacity: 0.9;
    border: 2px;
    border-radius: 40px;
}

.username {
    position: inherit;
    left: 20%;
    width: 60%;
    top: 20%;
    border: 1px solid black;
}

.password {
    position: inherit;
    left: 20%;
    width: 60%;
    top: 30%;
    border: 1px solid black;
}

.button {
    position: inherit;
    left: -20%;
    width: 20%;
    top: 50%;
}